<template>
  <div>  
    <a class="btn-small btn-floating" @click="test"><i class="material-icons">autorenew</i></a>
    <div style="display:flex;justify-content:flex-start;position:fixed;z-index:1;top:0;left:0;">
      <div>
        <a class="btn-floating btn-small waves-effect waves-light" style="margin-top:50px;margin-left:50px;" @click="showSettings = !showSettings">
          <i class="material-icons">{{showSettings ? 'close' : 'settings'}}</i>
          </a>
      </div>
      <div style="width:100vw;height:100vh;background-color:lightgray;position:fixed;padding:50px 0;overflow-y:auto" v-if="showSettings">
          <div>
            <h4 class="teal-text">activity level</h4>
            <form action="" style="text-align:left;margin:0 35%;">
              <p>
                <label for="light">
                  <input type="radio" name="level" value="light" id="light" v-model="settings.activityLevel">
                  <span>light</span>
                </label>
              </p>
              <p>
                <label for="moderate">
                  <input type="radio" name="level" value="moderate" id="moderate" v-model="settings.activityLevel">
                  <span>moderate</span>
                </label>
              </p>
              <p>
                <label for="active">
                  <input type="radio" name="level" value="active" id="active" v-model="settings.activityLevel">
                  <span>active</span>
                </label>
              </p>
              <div style="text-align:right;">
                <button class="btn waves-effect waves-light" @click="saveSettings">save</button>              
              </div>
            </form>
          </div>
          <div class="horizontal-divider"></div>
          <div style="margin-botom:50px;">
            <h4 class="teal-text">custom activities</h4>
            <div v-if="customMovements.length > 0">
              <div v-for="activity in customMovements" v-bind:key="activity.id" style="margin-top:5px;display:flex;align-items:center;justify-content:center;">
                <div style="font-size:14px;position:relative;top:-5px;">
                  {{activity.name}}  {{activity.reps}}
                </div>
                <div style="margin-left:15px;">
                  <a class="teal-text"><i class="material-icons" @click="removeCustomActivity(activity.id)">close</i></a>
                </div>
              </div>
            </div>
            <div v-if="customMovements.length == 0">
              you haven't added any custom activities yet...
            </div>
            <div class="horizontal-divider"></div>
            <div>
              <h4 class="teal-text">add an activity</h4>
              <div class="row input-field" style="width:50%;">
                <label for="activity-name">name</label>
                <input type="text" id="activity-name" name="activity-name" v-model="newCustomActivity.name">
              </div>
              <div class="row input-field" style="width:50%;">
                <label for="activity-reps">reps / detail</label>
                <input type="text" id="activity-reps" name="activity-reps" v-model="newCustomActivity.reps">
              </div>
            </div>
            <a class="btn waves-effect waves-light" @click="addCustomActivity">add</a>
          </div>
      </div>
    </div>
    <div v-if="!showSettings">
      <div style="display:flex;justify-content:center; align-items:center;">
        <div style="font-size:9rem;" v-bind:class="counterInterval != null && seconds > 0 && !isRunning ? 'grey-text text-darken-2' : 'teal-text'" >{{getTimeDisplay}}</div>
      </div>
      
      <a v-if="!isRunning && counterInterval == null" class="btn-floating btn-large pulse" @click="startTimer"><i class="material-icons">play_arrow</i></a>
      <a v-if="counterInterval != null && seconds > 0" class="btn-floating btn-large" v-bind:class="{'pulse': !isRunning}" @click="togglePause"><i class="material-icons">{{isRunning ? 'pause' : 'play_arrow'}}</i></a>
      <div style="margin-top:20px;">
        <a v-if="counterInterval" class="btn-floating btn-small deep-orange lighten-1" @click="resetTimer"><i class="material-icons">clear</i></a>
      </div>
    
      <div v-if="suggestedMovement" style="margin-top-20px;">
        <h2>{{suggestedMovement.name}}</h2>
        <h4>{{suggestedReps}}</h4>
      </div>
      
      <!-- test -->
      <!-- <div v-for="movement in movements" v-bind:key="movement.id">
        {{movement.name}}  {{movement.reps}}
      </div> -->

  </div>
    
  </div>
</template>

<script>

export default {
  name: 'Getup',
  data: function(){
    return {
      durationMinutes: 60,
      seconds: '',
      counterInterval: null,
      isRunning: false,
      movements: [],
      customMovements: [],
      suggestedMovement: null,
      suggestedReps: '',
      showSettings: false,
      newCustomActivity: new Object(),
      settings: { activityLevel: 'light' }
    }
  },
  methods: {
    adjustDuration: function(increase){
      if(increase){
        this.durationMinutes += 30;
      } else if (this.durationMinutes >= 60){
        this.durationMinutes -= 30;
      }
      this.initTimer();
    },
    test: function(){
      this.durationMinutes = 0.1;
      this.initTimer();
    },
    getMovements : function(){
      return true;
    },
    getRandomMovement: function(){
      var min = 0;
      var max = Math.floor(this.movements.length-1);
      this.suggestedMovement = this.movements[Math.floor(Math.random() * (max-min)) + min];
      this.suggestedReps = this.getSuggestedReps();
      // alert("It's been " + this.durationMinutes + ". Time to get up and move :)");
      if(Notification.permission == 'granted'){
        var notification = new Notification('Time for some action', {body: this.suggestedMovement.name });
        console.log('notification: ', notification);
      }

    },
    getSuggestedReps: function(){
      var rx = [{'level':'light', 'repetition': '2 sets of', 'time': '20 seconds'}, 
      {'level': 'moderate', 'repetition': '3 sets of', 'time': '40 seconds'},
      {'level': 'active', 'repetition': '4 sets of', 'time': '60 seconds'}];

      var reps = rx.filter(rec => rec.level == this.settings.activityLevel)[0][this.suggestedMovement.movementType];
      if (this.suggestedMovement.movementType == "repetition")
        reps += ' ' + this.suggestedMovement.reps;
        return reps;
    },
    startTimer : function(){
      var getup = this;
      getup.suggestedMovement = null;
      this.counterInterval = setInterval(function(){        
        getup.seconds--;
        if(getup.seconds == 0){
          getup.getRandomMovement();
          getup.stopTimer();        
        }
      }, 1000);
      this.isRunning = true;
    },
    stopTimer: function(){
      clearInterval(this.counterInterval);
      this.isRunning = false;
    },
    togglePause: function(){
      if(this.isRunning){
        this.stopTimer();
      } else {
        this.startTimer();
      }
    },
    initTimer: function(){
      this.seconds = this.durationMinutes * 60;
      this.counterInterval = null;
      this.suggestedMovement = null;
      
    },
    resetTimer: function(){
      this.stopTimer();
      this.durationMinutes = 60;
      this.seconds = this.durationMinutes * 60;
      this.counterInterval = null;
      this.suggestedMovement = null;
    },
    saveSettings: function(){
      localStorage.setItem('getupSettings', JSON.stringify(this.settings));
    },
    addCustomActivity: function(){
      var customActivity = { id: 'custom-' + new Date()
                            , name: this.newCustomActivity.name
                            , movementType: 'custom'
                            , reps: this.newCustomActivity.reps };
      this.customMovements.push(customActivity);
      this.movements.push(customActivity);
      this.newCustomActivity = {};
      localStorage.setItem('getupCustomActivities', JSON.stringify(this.customMovements));
    },
    removeCustomActivity: function(id){
      this.customMovements.find((el, index)=> {
        if(el && el.id == id){
          this.customMovements.splice(index, 1);
          localStorage.setItem('getupCustomActivities', JSON.stringify(this.customMovements));
        }
      });
      this.movements.find((el, index) => {
        if(el && el.id == id){
          this.movements.splice(index, 1);
        }
      });
      
    }

  },
  computed: {
    getTimeDisplay: function(){
      return Math.floor(this.seconds/60) + ':' + (Math.round(this.seconds % 60).toString().length > 1 ? Math.round(this.seconds % 60) : '0' + Math.round(this.seconds % 60));
    }
  },
  mounted: function(){
    this.resetTimer();
    //todo: check if first load
    //todo: yes --> allow notifications? -- save setting and apply
    //todo: no --> check allow notifications setting -- if exists, apply, else ask again

    var getup = this;
    if (!("Notification" in window)) {
      console.log("This browser does not support desktop notification");
    } else if( Notification.permission != 'granted' && Notification.permission != 'denied'){
      Notification.requestPermission().then((res) => console.log('notification permission: ',res));      
    }

    fetch('movements.json').then(resp => resp.json()).then(converted => {
      getup.movements  = converted.data;
      //todo: get custom activities from local storage and append to getup.movements ++ put in this.customMovements[]
      if(localStorage.getItem('getupSettings')){
        console.log('heyy ', localStorage.getItem('getupSettings'));
        this.settings = JSON.parse(localStorage.getItem('getupSettings'));
      }

      if(localStorage.getItem('getupCustomActivities')){
        console.log('heyy ', localStorage.getItem('getupCustomActivities'));
        this.customMovements = JSON.parse(localStorage.getItem('getupCustomActivities'));
        this.customMovements.forEach((movement)=> getup.movements.push(movement));
      }
    });


  }
}
</script>

<style>
  .horizontal-divider {
    border-bottom: 2px solid #5c6bc0 ;
    width: 50%;
    margin: 30px 25%;
    text-align:center;
  }
</style>